<template>
  <div id="collect">
    <mt-header fixed title="我的收藏">
      <mt-button slot="right" @click="clear">清空收藏</mt-button>
    </mt-header>
    <div class="list" v-for="(item,index) of list" :key="index">
      <van-cell class="item">
        <van-tag mark size="medium" type="success" class="tag">{{item.category}}</van-tag>
        <van-tag size="large" type="danger" class="del" @click="del(index)">删除</van-tag>
        <div class="title">{{item.title}}</div>
        <img :src="item.pic" alt />
        <div class="detail">
          <span>来源：{{item.src}}</span>
          <span class="time">{{item.time}}</span>
        </div>
      </van-cell>
    </div>
    <van-empty image="error" description="收藏夹一尘不染~" v-if="empty" />
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: [],
      empty: false
    };
  },
  methods: {
    del(i) {
      this.list.splice(i, 1);
      if (this.list.length === 0) {
        this.empty = true;
      }
      // console.log(this.list);
      let str = JSON.stringify(this.list);
      window.localStorage.setItem("collect", str);
    },
    clear() {
      this.$messagebox({
        title: "提示",
        message: "您正在清空全部收藏",
        showCancelButton: true
      }).then(res => {
        if (res === "confirm") {
          this.list.splice(0);
          this.empty = true;
          let str = JSON.stringify(this.list);
          window.localStorage.setItem("collect", str);
        }
      });
    }
  },
  mounted() {
    let listArr = window.localStorage.getItem("collect");
    if (JSON.parse(listArr).length == 0) {
      this.empty = true;
    } else {
      this.empty = false;
      this.list = JSON.parse(window.localStorage.getItem("collect"));
    }
  }
};
</script>
<style scoped>
#collect {
  margin-top: 50px;
  padding-bottom: 60px;
}

.list {
  padding: 0 5px;
}

.title {
  width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 17px;
  font-weight: bold;
  margin-bottom: 10px;
}

.item {
  position: relative;
  margin-top: 10px;
  background-color: oldlace;
  border-radius: 5px;
}

.item img {
  max-width: 95%;
  padding: 2px;
  border-radius: 5px;
  border: 4px solid lightblue;
  /* color:lightblue; */
}

.tag {
  position: absolute;
  transform: rotate(30deg);
  right: -6px;
  top: 5px;
}

.del {
  position: absolute;
  bottom: 0;
  right: 2px;
}

.detail {
  float: left;
  font-size: 13px;
  color: #787878;
}

.time {
  margin-left: 10px;
}
</style>